<template>
  <div id="app" :class="{ locked: showNav }">
   
    <Burger 
    @click.native="toggleScroll"
    v-if="mobileView"/>
    <NavMobile
     :class="{ open: showNav }"
     @sendData="toggleScroll"/>
    <div class="content" :class="{ opened: showNav }">
      <Preloader/>
      <ScreenLocker v-if="showNav" />
      <a name="home"></a>
      <Header v-if="!mobileView"/>
      <Main />
      <Skills />
      <About />
      <Portfolio />
      <Footer />
    </div>
  </div>
</template>

<script>
import Header from './components/Header';
import Main from './components/Main';
import About from './components/About';
import Skills from './components/Skills';
import Portfolio from './components/Portfolio';
import Footer from './components/Footer';
import Burger from './components/Burger';
import NavMobile from './components/NavMobile';
import Preloader from './components/Preloader';
import ScreenLocker from './components/ScreenLocker';
export default {
  name: 'App',
  components: {
    Header,
    Main,
    About,
    Skills,
    Portfolio,
    Footer,
    Burger,
    NavMobile,
    Preloader,
    ScreenLocker
  },
  data() {
      return {
          mobileView: true,
          showNav: false,
      }
  },
  methods: {
    handleView() {
      this.mobileView = window.innerWidth <= 768;
    },
    toggleScroll() {
      document.body.classList.toggle('scroll');
      this.showNav = !this.showNav;
      console.log('It works?');
    }
  },
  created() {
    this.handleView();
    window.addEventListener('resize', this.handleView);
    console.log("for more info: https://www.youtube.com/watch?v=dQw4w9WgXcQ");
  }
}

</script>

<style lang="scss">
  body {
    font-family: Arial, sans-serif;
    font-size: 18px;
    background: #FFFFFF;
  }
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  .scroll {
    overflow: hidden;
    position: relative;
    max-height: 100vh;
    max-width: 100vw;
    .locked {
      position: relative;
      overflow: hidden;
      max-height: 100%;
      max-width: 100vw;
    }
    .content {
      position: relative;
      overflow: hidden;
      max-height: 100%;
      max-width: 100vw;
    }
  }
  .content {
    position: absolute;
    top: 0;
    transition: 1s cubic-bezier(0,.12,.14,1);
  }
  .open {
    z-index: 3;
  }
  .opened {
    transform: translateX(50vw);
  }
  @media(max-width: 576px) {
    body {
      font-size: 9px;
      $main-container: 80%;
    }
  }
  @media(min-width: 576px) {
    body {
      font-size: 10px;
    }
  }
  @media(min-width: 768px) {
    body {
      font-size: 12px;
    }
  }
  @media(min-width: 992px) {
    body {
      font-size: 14px;
    }
  }
  @media(min-width: 1200x) {
    body {
      font-size: 16px;
    }
  }
</style>
